<template>
  <div class="tab3">
    <!-- <TabBar /> -->
    <!-- title -->
    <div class="van-nav-bar van-hairline--bottom" style="position: fixed; width: 100%;top: 0;">
      <div class="van-nav-bar__left" >
        <!-- <i class="van-icon van-icon-arrow-left van-nav-bar__arrow"> </i> -->
        <span class="van-nav-bar__text"></span>
      </div>
      <div class="van-nav-bar__title van-ellipsis"  >
            <van-tabs v-model="title" type="card" @click="onType" style="margin-top: 8px;">
                <van-tab title="投注记录" name="投注记录"> </van-tab>
                <van-tab title="追号记录" name="追号记录"> </van-tab>
            </van-tabs>
        </div>
      <div class="van-nav-bar__right" >
        <span class="van-nav-bar__text">
          <!-- <i class="van-icon van-icon-wap-nav" style="font-size: 22px;"> </i> -->
        </span>
      </div>
    </div>

    <!-- <router-view /> -->
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
        <Touzhu v-if="title == '投注记录'" :index_t="index_t" />
        <Zhuihao v-if="title == '追号记录'" :index_z="index_z" />
    </van-pull-refresh>
    
    <van-tabbar route>
      <van-tabbar-item replace to="/" icon="wap-home">首页</van-tabbar-item>
      <van-tabbar-item replace to="/tab2" icon="hot">大厅</van-tabbar-item>
      <van-tabbar-item replace to="/tab3" icon="medal">记录</van-tabbar-item>
      <van-tabbar-item replace to="/tab4" icon="friends">优惠</van-tabbar-item>
      <van-tabbar-item replace to="/tab5" icon="friends">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import TabBar from "@/components/NavBar/TabBar.vue";
import Touzhu from "./views/touzhu.vue";
import Zhuihao from "./views/zhuihao.vue";
export default {
    name:"tab3",
  components: {
    TabBar,
    Touzhu,
    Zhuihao
  },
  data() {
    return {
      // 下拉刷新
      isLoading: false,
      title: '',
      index_t: '',
      index_z: ''
    };
  },
  mounted: function() {
    if( this.$route.query.data != undefined ){
      this.$toast(this.$route.query.data);
      this.title = this.$route.query.data;
      this.title=='投注记录'?this.index_ = '未开奖':this.index_ = '全部';
    }else{
      this.title = '投注记录';
      this.index_ = '全部'
    }
  },
  computed: {},
  methods: {
      // 下拉加载  
      onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1);
        }

        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 1000);
    },
    // 投注追号切换
    onType(name, title) {
        this.title = title;
    },
    // 下拉刷新
    onRefresh() {
      setTimeout(() => {
        this.$toast("刷新成功");
        this.isLoading = false;
        this.count++;
      }, 1000);
    }
  }
};
</script>

