<script>
import TabBar from "@/components/NavBar/TabBar.vue";
export default {
  props:['index_t'],
  components: {
    TabBar
  },
  data() {
    return {
        title:'',
      list: [],
      loading: false,
      finished: false
    };
  },
  mounted: function() { 
      this.title = this.index_t;
  },
  computed: {},
  methods: {
      // 下拉加载  
      onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1);
        }

        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 1000);
    },
    // tab栏切换
    onClick(name, title) {
      this.$toast(title);
    },
   info(){
       this.$router.push({
        path:'/touZhuInfo',
        query: {}
        })
   }
  }
};
